.tcp__dapostcard {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    max-width: 1400px;
    padding: 2rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 102, 240, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 40px;
}

.tcp__dapostcard-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
}

.tcp__dapostcard-title h1 {
    padding: 1rem;
}

.tcp__dapostcard-title svg {
    width: 30px;
    margin-left: 2rem;
}

.tcp__dapostcard-time {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.tcp__dapostcard-excerpt {
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
    align-items: center;
}

.postcard-btn {
    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff6a 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #00ff2a 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;

    font-family: var(font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;

    border: none;
    width: 150px;
    padding: 0.5rem 2rem;
    margin-top: 2rem;
    color: #fff;
    cursor: pointer;

}
