.tcp__brand-top {
display: flex;
flex-direction: column;
align-items: center;
}

.tcp__brand-top h1{
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 32px;
    margin-top: 4rem;
}

.tcp__brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.tcp__brand div {
    flex: 1;
    max-width: 120px;
    min-width: 90px;
    margin: 1rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.tcp__brand img {
    width: 100px;
    cursor: pointer;
}

.tooltip-brand {
    position: relative;
    display: inline-block;
  }
  
  /* Tooltip text */
  .tooltip-brand .tooltiptext-brand {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all 1s ease-out 0s;
    -moz-transition: all 1s ease-out 0s;
    -ms-transition: all 1s ease-out 0s;
    -o-transition: all 1s ease-out 0s;
    transition: all 1s ease-out 0s;
  
    font-family: var(--font-family);
    font-size: 14px;
  
    color: var(--color-text);
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
   
    /* Position the tooltip text */
    position: absolute;
    z-index: 1;
    width: 300px;
    bottom: 100%;
  }
  
  /* Show the tooltip text when mouse over the tooltip container */
  .tooltip-brand:hover .tooltiptext-brand {
    visibility: visible;
    -webkit-opacity: 1;
    -moz-opacity: 1;
    opacity: 1;
  }

@media screen and (max-width: 850px) {
    .tcp__brand-top h1 {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: auto;
        margin-top: 40px;
    }
}

@media screen and (max-width: 650px) {
    .tcp__brand-top h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 410px) {
    .tcp__brand-top h1 {
        font-size: 24px;
        line-height: 36px;
    }
}