* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: var(--font-family);
    font-size: 14px;
    color: #fff;
}

body {
    min-height: 100vh;
    background-color: dodgerblue;
}

.SignIn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh; 
  padding: 1rem 0.5rem;
}

.SignUp {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh; 
    padding: 1rem 0.5rem;
  }

section {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 1rem;
    background-color: rgba(0, 61, 10, 0.8);
    border-radius: 20px;
}

form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    flex-grow: 1;
    padding-bottom: 1rem;
}

a, a:visited {
    color: #fff;
}

input[type="text"],
input[type="password"],
button,
textarea {
  font-family: var(--font-family);
  font-size: 16px;
  padding: 0.25rem;
  border-radius: 0.5rem;
}

label {
  margin-top: 1rem;
}

.btn-signbar {
    margin-top: 2rem;
    margin-left: auto;
    margin-right: auto;
    padding: 0.5rem;
    width: 150px;
}

.instructions {
    font-size: 14px;
    border-radius: 0.5rem;
    background: #000;
    color: #fff;
    padding: 0.25rem;
    position: relative;
    bottom: -10px;
}

.instructions > svg {
    margin-right: 0.25rem;
}

.offscreen {
    position: absolute;
    left: -9999px;
}

.hide {
    display: none;
}

.valid {
    color: limegreen;
    margin-left: 0.25rem;
}

.invalid {
    color: red;
    margin-left: 0.25rem;
}

.errmsg {
    color: firebrick;
    font-weight: bold;
    padding: 0.5rem;
    margin-bottom: 0.5rem;
}

.line {
    display: inline-block;
}

.tooltip {
    position: relative;
    display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all 1s ease-out 0s;
    -moz-transition: all 1s ease-out 0s;
    -ms-transition: all 1s ease-out 0s;
    -o-transition: all 1s ease-out 0s;
    transition: all 1s ease-out 0s;  
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
   
    /* Position the tooltip text */
    position: absolute;
    z-index: 1;
    width: 300px;
    top: 0;
    left: -300px;
  }
  
  /* Show the tooltip text when mouse over the tooltip container */
  .tooltip:hover .tooltiptext {
    visibility: visible;
    -webkit-opacity: 1;
    -moz-opacity: 1;
    opacity: 1;
  }

  .tooltip .tooltiptext p{
    text-align: right;
    color: var(--color-text-yellow);
    font-size: 14px;
    font-weight: 600;
    line-height: 18px;
    margin-right: 20px;
  }

  .tooltip input {
    width: 100%;
  }