@property --rotate {
    syntax: "<angle>";
    initial-value: 132deg;
    inherits: false;
}
  
:root {
    --card-height: 300px;
    --card-width: 250px;
}
  
.card {
    width: var(--card-width);
    height: var(--card-height);
    padding: 3px;
    position: relative;

    background: linear-gradient(180deg, #4c59ce 1.31%, #030236 107.96%);
    box-shadow: 0px 5.5px 15px rgba(0, 0, 0, 0.3);
    border-radius: 20px;

    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    color: rgb(88 199 250 / 0%);
    cursor: pointer;

    padding: 1rem;
    margin : 2rem 3rem;
    
}
  
.card:hover {
    color: var(--color-text);
    transition: color 1s;
}

.card:hover:before, .card:hover:after {
    animation: none;
    opacity: 0;
}
  
.card::before {
    content: "";
    width: 101%;
    height: 101%;
    border-radius: 22px;
    background-image: linear-gradient(var(--rotate), #fffc5dd3, #3c67e3 43%, #4e00c2);
    position: absolute;
    z-index: -1;
    top: -0.5%;
    left: -0.5%;
    animation: spin 2.5s linear infinite;
}
  
.card::after {
    position: absolute;
    content: "";
    top: calc(var(--card-height) / 6);
    left: 0;
    right: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
    margin: 0 auto;
    transform: scale(0.8);
    filter: blur(calc(var(--card-height) / 6));
    background-image: linear-gradient(var(--rotate), #5ddcff, #3c67e3 43%, #4e00c2);
    opacity: 1;
    transition: opacity .5s;
    animation: spin 2.5s linear infinite;
}

@keyframes spin {
    0% {
      --rotate: 0deg;
    }
    100% {
      --rotate: 360deg;
    }
}

.tcp__features-container__feature-title div {
    margin-left: 0;
    width: 20px;
    height: 3px;
    background: linear-gradient(5.12deg, #FFE600 -13.11%, #FFD84F 63.91%, #FFFFFF 147.63%);
}

.tcp__features-card {
    margin-top: 30px;
}

.tcp__features-card img {
    border-radius: 10px;
}

.tcp__features-card h1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 50px;
    
    text-align: center;
    letter-spacing: -0.04em;
    cursor: pointer;
}

.tooltipcard {
    position: relative;
    display: inline-block;
  }
  
  /* Tooltip text */
  .tooltipcard .tooltiptextcard {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all 1s ease-out 0s;
    -moz-transition: all 1s ease-out 0s;
    -ms-transition: all 1s ease-out 0s;
    -o-transition: all 1s ease-out 0s;
    transition: all 1s ease-out 0s;
  
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
  
    color: var(--color-text);
    text-align: center;
    padding: 0;
    border-radius: 6px;
   
    /* Position the tooltip text */
    position: absolute;
    top: -100px;
    left: -40px;
    z-index: 1;
    width: 300px;
    bottom: 100%;
  }
  
  /* Show the tooltip text when mouse over the tooltip container */
  .tooltipcard:hover .tooltiptextcard {
    visibility: visible;
    top: -40px;
    -webkit-opacity: 1;
    -moz-opacity: 1;
    opacity: 1;
  }