@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap');

:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-bar: linear-gradient(103.22deg, #16bd00 -13.86%, #16bd00 99.55%);
  --gradient-bar1: linear-gradient(103.22deg, #94af4b -13.86%, #94af4b 99.55%);
  --gradient-pad: linear-gradient(180deg, rgba(0, 37, 14, 0.829) 21.8%, rgba(255, 255, 255, 0) 99.06%);
  
  --color-bg: #040D07;
  --color-bg-green: #005c1f;
  --color-bg-lightgreen: #648f67;
  --color-footer : #055647;
  --color-blog: #042c54;
  --color-text: #fff;
  --color-text-white: #fff;
  --color-text-yellow: rgb(255, 217, 2);
  --color-text-gray: rgb(92, 92, 92);
  --color-text-blue: rgb(4, 8, 252);
  --color-text-rose: rgb(253, 143, 238);
  --color-text-black: rgb(0, 0, 0);
  --color-subtext: #ffc401;
}